/**
 * Folder exclusion utilities
 *
 * Handles checking if a folder path is excluded from Zest tracking
 */

import { normalize, resolve, sep } from "node:path";
import type { UserSettings } from "../config/settings.js";

/**
 * Check if the current platform uses case-insensitive filesystem
 */
function isCaseInsensitiveFilesystem(): boolean {
  return process.platform === "win32" || process.platform === "darwin";
}

/**
 * Normalize path for comparison, handling case-insensitive filesystems
 */
function normalizeForComparison(path: string): string {
  const normalized = normalize(resolve(path));
  return isCaseInsensitiveFilesystem() ? normalized.toLowerCase() : normalized;
}

/**
 * Check if a folder path is excluded from tracking
 *
 * @param folderPath - The folder path to check
 * @param settings - User settings containing excludedFolders list
 * @returns true if the folder (or any parent) is in the exclusion list
 */
export function isFolderExcluded(folderPath: string, settings: UserSettings): boolean {
  const comparePath = normalizeForComparison(folderPath);
  const separatorForComparison = isCaseInsensitiveFilesystem() ? sep.toLowerCase() : sep;

  return settings.excludedFolders.some((excluded) => {
    const compareExcluded = normalizeForComparison(excluded);
    // Check if path equals or is inside excluded folder
    return (
      comparePath === compareExcluded ||
      comparePath.startsWith(compareExcluded + separatorForComparison)
    );
  });
}

/**
 * Add a folder to the exclusion list
 *
 * @param folderPath - The folder path to exclude
 * @param settings - User settings to modify
 * @returns Updated settings with the folder added (or unchanged if already present)
 */
export function addExcludedFolder(
  folderPath: string,
  settings: UserSettings,
): { settings: UserSettings; alreadyExcluded: boolean } {
  const normalizedPath = normalize(resolve(folderPath));
  const comparePath = normalizeForComparison(folderPath);

  // Check if already excluded (case-insensitive on Windows/macOS)
  const alreadyExists = settings.excludedFolders.some(
    (f) => normalizeForComparison(f) === comparePath,
  );

  if (alreadyExists) {
    return { settings, alreadyExcluded: true };
  }

  return {
    settings: {
      ...settings,
      excludedFolders: [...settings.excludedFolders, normalizedPath],
    },
    alreadyExcluded: false,
  };
}

/**
 * Remove a folder from the exclusion list
 *
 * @param folderPath - The folder path to remove from exclusion
 * @param settings - User settings to modify
 * @returns Updated settings with the folder removed (or unchanged if not present)
 */
export function removeExcludedFolder(
  folderPath: string,
  settings: UserSettings,
): { settings: UserSettings; wasExcluded: boolean } {
  const comparePath = normalizeForComparison(folderPath);

  // Find and remove matching folder (case-insensitive on Windows/macOS)
  const matchIndex = settings.excludedFolders.findIndex(
    (f) => normalizeForComparison(f) === comparePath,
  );

  if (matchIndex === -1) {
    return { settings, wasExcluded: false };
  }

  return {
    settings: {
      ...settings,
      excludedFolders: settings.excludedFolders.filter((_, i) => i !== matchIndex),
    },
    wasExcluded: true,
  };
}
