/**
 * Status Cache Manager
 *
 * Manages a shared status cache file that stores:
 * - Version update check results
 * - Sync error state
 *
 * All write operations use file locking to prevent race conditions between
 * concurrent processes (daemon, session handlers, commands).
 */

import { readFileSync, writeFileSync } from "node:fs";
import { STATUS_CACHE_FILE } from "../config/constants.js";
import { withFileLock } from "./file-lock.js";
import { logger } from "./logger.js";

/**
 * Sync error types - categorizes different failure modes
 */
export type SyncErrorType =
  | "not_authenticated" // Auth token expired/missing - user needs to run /zest:login
  | "upload_failed" // Upload to Supabase failed after retries
  | "network_error" // Network connectivity issue
  | "daemon_not_running" // Daemon process is not running
  | "daemon_restart_failed"; // Daemon failed to restart

/**
 * Version check data structure
 */
export interface VersionCheck {
  updateAvailable: boolean;
  currentVersion: string;
  latestVersion: string;
  checkedAt: number; // Unix timestamp
}

/**
 * Sync status data structure
 */
export interface SyncStatus {
  hasError: boolean;
  errorType: SyncErrorType | null;
  errorMessage: string | null;
  lastErrorAt: number | null; // Unix timestamp
  lastSuccessAt: number | null; // Unix timestamp
}

/**
 * Dev mode status data structure
 */
export interface DevModeStatus {
  active: boolean;
}

/**
 * Complete status cache structure
 */
export interface StatusCache {
  versionCheck: VersionCheck;
  syncStatus: SyncStatus;
  devMode?: DevModeStatus;
}

/**
 * Default version check state (no update available)
 */
const DEFAULT_VERSION_CHECK: VersionCheck = {
  updateAvailable: false,
  currentVersion: "unknown",
  latestVersion: "unknown",
  checkedAt: 0,
};

/**
 * Default sync status state (no error)
 */
const DEFAULT_SYNC_STATUS: SyncStatus = {
  hasError: false,
  errorType: null,
  errorMessage: null,
  lastErrorAt: null,
  lastSuccessAt: null,
};

/**
 * Default dev mode status state (not active)
 */
const DEFAULT_DEV_MODE_STATUS: DevModeStatus = {
  active: false,
};

/**
 * Default status cache state
 */
const DEFAULT_STATUS_CACHE: StatusCache = {
  versionCheck: DEFAULT_VERSION_CHECK,
  syncStatus: DEFAULT_SYNC_STATUS,
  devMode: DEFAULT_DEV_MODE_STATUS,
};

/**
 * Read the status cache file
 * Handles migration from old flat structure to new nested structure
 * Returns defaults if file doesn't exist or is corrupted
 *
 * @returns StatusCache with defaults for missing fields
 */
export function readStatusCache(): StatusCache {
  try {
    const data = readFileSync(STATUS_CACHE_FILE, "utf-8");
    const parsed = JSON.parse(data);

    // Check if this is the old flat structure (from update-check.json)
    // Old structure: { updateAvailable, currentVersion, latestVersion, checkedAt }
    // New structure: { versionCheck: {...}, syncStatus: {...} }
    if (parsed.updateAvailable !== undefined && !parsed.versionCheck) {
      // Migration: convert old flat structure to new nested structure
      logger.info("Migrating old update-check.json format to new status-cache.json format");

      const migrated: StatusCache = {
        versionCheck: {
          updateAvailable: parsed.updateAvailable ?? false,
          currentVersion: parsed.currentVersion ?? "unknown",
          latestVersion: parsed.latestVersion ?? "unknown",
          checkedAt: parsed.checkedAt ?? 0,
        },
        syncStatus: DEFAULT_SYNC_STATUS,
      };

      return migrated;
    }

    // New structure - merge with defaults to handle missing fields
    return {
      versionCheck: {
        ...DEFAULT_VERSION_CHECK,
        ...parsed.versionCheck,
      },
      syncStatus: {
        ...DEFAULT_SYNC_STATUS,
        ...parsed.syncStatus,
      },
      devMode: {
        ...DEFAULT_DEV_MODE_STATUS,
        ...parsed.devMode,
      },
    };
  } catch (error) {
    // File doesn't exist or is corrupted - return defaults
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      logger.debug("Status cache file does not exist, using defaults");
    } else {
      logger.warn("Failed to read status cache file, using defaults", error);
    }
    return DEFAULT_STATUS_CACHE;
  }
}

/**
 * Write version check data to the status cache
 * Preserves existing sync status data
 * Uses file locking to prevent race conditions
 *
 * Note: Errors are logged but not thrown to avoid disrupting caller workflows
 *
 * @param check - Version check data to write
 */
export async function writeVersionCheck(check: VersionCheck): Promise<void> {
  try {
    await withFileLock(STATUS_CACHE_FILE, async () => {
      // Read current cache inside lock
      const currentCache = readStatusCache();

      // Update only the version check section
      const updatedCache: StatusCache = {
        ...currentCache,
        versionCheck: check,
      };

      // Write back to file
      writeFileSync(STATUS_CACHE_FILE, JSON.stringify(updatedCache, null, 2), "utf-8");

      logger.debug("Wrote version check to status cache", {
        updateAvailable: check.updateAvailable,
        currentVersion: check.currentVersion,
        latestVersion: check.latestVersion,
      });
    });
  } catch (error) {
    // Log error but don't throw to avoid disrupting caller workflows
    logger.error("Failed to write version check to status cache", error);
  }
}

/**
 * Write sync status data to the status cache
 * Preserves existing version check data
 * Uses file locking to prevent race conditions
 *
 * Note: Errors are logged but not thrown to avoid disrupting caller workflows
 *
 * @param status - Sync status data to write
 */
export async function writeSyncStatus(status: SyncStatus): Promise<void> {
  try {
    await withFileLock(STATUS_CACHE_FILE, async () => {
      // Read current cache inside lock
      const currentCache = readStatusCache();

      // Update only the sync status section
      const updatedCache: StatusCache = {
        ...currentCache,
        syncStatus: status,
      };

      // Write back to file
      writeFileSync(STATUS_CACHE_FILE, JSON.stringify(updatedCache, null, 2), "utf-8");

      logger.debug("Wrote sync status to status cache", {
        hasError: status.hasError,
        errorType: status.errorType,
      });
    });
  } catch (error) {
    // Log error but don't throw to avoid disrupting caller workflows
    logger.error("Failed to write sync status to status cache", error);
  }
}

/**
 * Clear sync error status (called on successful sync)
 * Updates lastSuccessAt timestamp and clears error state
 * Uses file locking to prevent race conditions
 *
 * Note: Errors are logged but not thrown to avoid disrupting caller workflows
 */
export async function clearSyncError(): Promise<void> {
  try {
    await withFileLock(STATUS_CACHE_FILE, async () => {
      // Read current cache inside lock
      const currentCache = readStatusCache();

      // Clear error state and update lastSuccessAt
      const clearedStatus: SyncStatus = {
        hasError: false,
        errorType: null,
        errorMessage: null,
        lastErrorAt: currentCache.syncStatus.lastErrorAt, // Preserve for debugging
        lastSuccessAt: Date.now(),
      };

      const updatedCache: StatusCache = {
        ...currentCache,
        syncStatus: clearedStatus,
      };

      // Write back to file
      writeFileSync(STATUS_CACHE_FILE, JSON.stringify(updatedCache, null, 2), "utf-8");

      logger.debug("Cleared sync error in status cache");
    });
  } catch (error) {
    // Log error but don't throw to avoid disrupting caller workflows
    logger.error("Failed to clear sync error in status cache", error);
  }
}

/**
 * Write dev mode active status to the status cache
 * Sets devMode.active = true to indicate dev environment is in use
 * Uses file locking to prevent race conditions
 *
 * Note: Errors are logged but not thrown to avoid disrupting caller workflows
 */
export async function writeDevModeActive(): Promise<void> {
  try {
    await withFileLock(STATUS_CACHE_FILE, async () => {
      // Read current cache inside lock
      const currentCache = readStatusCache();

      // Update dev mode status
      const updatedCache: StatusCache = {
        ...currentCache,
        devMode: { active: true },
      };

      // Write back to file
      writeFileSync(STATUS_CACHE_FILE, JSON.stringify(updatedCache, null, 2), "utf-8");

      logger.debug("Wrote dev mode active to status cache");
    });
  } catch (error) {
    // Log error but don't throw to avoid disrupting caller workflows
    logger.error("Failed to write dev mode to status cache", error);
  }
}
