/**
 * Session management
 *
 * Handles session persistence, validation, and token refresh
 */

import { readFile, unlink, writeFile } from "node:fs/promises";
import { dirname } from "node:path";
import { createClient } from "@supabase/supabase-js";
import type { Database } from "@zest/types/database";
import {
  AUTH_SESSION_LOAD_FAILED,
  AUTH_SESSION_SAVE_FAILED,
  AUTH_TOKEN_REFRESH_FAILED,
} from "../analytics/events.js";
import { captureException } from "../analytics/index.js";
import { buildAuthProperties, buildFileSystemProperties } from "../analytics/properties.js";
import {
  PROACTIVE_REFRESH_THRESHOLD_MS,
  SESSION_FILE,
  SUPABASE_ANON_KEY,
  SUPABASE_URL,
} from "../config/constants.js";
import { ensureDirectory } from "../utils/fs-utils.js";
import { logger } from "../utils/logger.js";

export interface AuthSession {
  accessToken: string;
  refreshToken: string;
  expiresAt: number; // Access token expiration (milliseconds)
  refreshTokenExpiresAt?: number; // Refresh token expiration (milliseconds) - optional for backward compatibility
  userId: string;
  email: string;
  workspaceId?: string;
  workspaceName?: string;
}

/**
 * Load session from file
 */
export async function loadSession(): Promise<AuthSession | null> {
  try {
    const content = await readFile(SESSION_FILE, "utf-8");
    const session = JSON.parse(content) as AuthSession;

    // Validate session structure
    if (
      !session.accessToken ||
      !session.refreshToken ||
      !session.expiresAt ||
      !session.userId ||
      !session.email
    ) {
      logger.warn("Invalid session structure, clearing session");
      await clearSession();
      return null;
    }

    const now = Date.now();

    // Check if refresh token is expired (only if expiration is set - many configs have refresh tokens that never expire)
    if (session.refreshTokenExpiresAt && session.refreshTokenExpiresAt < now) {
      logger.warn("Refresh token expired, user must re-authenticate");
      await clearSession();
      return null;
    }

    // Check if access token is expired
    if (session.expiresAt < now) {
      logger.debug("Access token expired, attempting refresh");
      try {
        return await refreshSession(session);
      } catch (error) {
        logger.warn("Failed to refresh session", error);
        await clearSession();
        return null;
      }
    }

    return session;
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      // File doesn't exist - not an error
      return null;
    }
    logger.error("Failed to load session", error);
    if (error instanceof Error) {
      captureException(error, AUTH_SESSION_LOAD_FAILED, "session-manager", {
        ...buildFileSystemProperties({
          filePath: SESSION_FILE,
          operation: "read",
          errnoCode: (error as NodeJS.ErrnoException).code,
        }),
      });
    }
    return null;
  }
}

/**
 * Save session to file with secure permissions
 */
export async function saveSession(session: AuthSession): Promise<void> {
  try {
    // Ensure directory exists
    await ensureDirectory(dirname(SESSION_FILE));

    // Write session file
    await writeFile(SESSION_FILE, JSON.stringify(session, null, 2), {
      encoding: "utf-8",
      mode: 0o600, // Readable/writable by owner only
    });

    logger.info("Session saved successfully");
  } catch (error) {
    logger.error("Failed to save session", error);
    if (error instanceof Error) {
      captureException(error, AUTH_SESSION_SAVE_FAILED, "session-manager", {
        ...buildFileSystemProperties({
          filePath: SESSION_FILE,
          operation: "write",
          errnoCode: (error as NodeJS.ErrnoException).code,
        }),
      });
    }
    throw error;
  }
}

/**
 * Clear session file
 */
export async function clearSession(): Promise<void> {
  try {
    await unlink(SESSION_FILE);
    logger.info("Session cleared successfully");
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      // File doesn't exist - not an error
      return;
    }
    logger.error("Failed to clear session", error);
    throw error;
  }
}

/**
 * Refresh an expired session using the refresh token
 * Uses Supabase JS client library for proper token handling
 */
export async function refreshSession(session: AuthSession): Promise<AuthSession> {
  try {
    const now = Date.now();
    const timeUntilExpiration = session.expiresAt - now;

    logger.debug("=== Starting Token Refresh ===");
    logger.debug(`Current access token expires at: ${new Date(session.expiresAt).toISOString()}`);
    logger.debug(`Time until expiration: ${Math.round(timeUntilExpiration / 1000)}s`);
    logger.debug(`Token is ${timeUntilExpiration < 0 ? "EXPIRED" : "still valid"}`);

    if (session.refreshTokenExpiresAt) {
      const timeUntilRefreshExpiration = session.refreshTokenExpiresAt - now;
      logger.debug(
        `Refresh token expires at: ${new Date(session.refreshTokenExpiresAt).toISOString()}`,
      );
      logger.debug(
        `Refresh token time remaining: ${Math.round(timeUntilRefreshExpiration / 1000)}s`,
      );
    } else {
      logger.debug("Refresh token: Never expires");
    }

    // Check configuration
    if (!SUPABASE_URL || !SUPABASE_ANON_KEY) {
      throw new Error("Supabase configuration missing (URL or anon key)");
    }

    logger.debug("Using Supabase JS client to refresh session");
    logger.debug(`Supabase URL: ${SUPABASE_URL}`);
    logger.debug(`Refresh token length: ${session.refreshToken.length} characters`);

    // Create a Supabase client for token refresh
    const supabase = createClient<Database>(SUPABASE_URL, SUPABASE_ANON_KEY, {
      auth: {
        persistSession: false,
        autoRefreshToken: false,
      },
    });

    // Use Supabase client's refreshSession method
    // This properly handles OAuth client association
    const { data, error } = await supabase.auth.refreshSession({
      refresh_token: session.refreshToken,
    });

    if (error) {
      logger.error(`Supabase refresh error: ${error.message}`, error);
      throw new Error(`Token refresh failed: ${error.message}`);
    }

    if (!data.session) {
      throw new Error("Token refresh failed: No session returned from Supabase");
    }

    logger.debug("Refresh response received successfully from Supabase");
    logger.debug(`New access token length: ${data.session.access_token.length} characters`);
    logger.debug(`New refresh token length: ${data.session.refresh_token.length} characters`);
    logger.debug(`Access token expires in: ${data.session.expires_in || "unknown"} seconds`);

    // Calculate access token expiration time
    let expiresAt: number;
    if (data.session.expires_at) {
      // Supabase returns expires_at in seconds, convert to milliseconds
      expiresAt = data.session.expires_at * 1000;
    } else if (data.session.expires_in) {
      // Fallback: use expires_in (seconds) to calculate expiration
      expiresAt = now + data.session.expires_in * 1000;
    } else {
      // Default: assume 1 hour expiration (Supabase default)
      expiresAt = now + 3600 * 1000;
      logger.warn("No expiration info from Supabase, assuming 1 hour");
    }

    // Refresh token expiration typically doesn't change, keep existing value
    const refreshTokenExpiresAt = session.refreshTokenExpiresAt;

    // Check if tokens actually changed
    const accessTokenChanged = session.accessToken !== data.session.access_token;
    const refreshTokenChanged = session.refreshToken !== data.session.refresh_token;

    logger.debug(`Access token changed: ${accessTokenChanged}`);
    logger.debug(`Refresh token changed: ${refreshTokenChanged}`);

    if (!accessTokenChanged) {
      logger.warn("  Access token did not change after refresh - this might indicate an issue");
    }
    if (!refreshTokenChanged) {
      logger.debug("Refresh token did not change (this is normal for Supabase)");
    }

    const newSession: AuthSession = {
      ...session,
      accessToken: data.session.access_token,
      refreshToken: data.session.refresh_token,
      expiresAt,
      refreshTokenExpiresAt,
      userId: data.session.user.id,
      email: data.session.user.email || session.email,
    };

    logger.debug(`New access token will expire at: ${new Date(expiresAt).toISOString()}`);
    logger.debug(
      `New expiration is ${Math.round((expiresAt - session.expiresAt) / 1000)}s from old expiration`,
    );

    if (refreshTokenExpiresAt) {
      logger.debug(
        `Refresh token will expire at: ${new Date(refreshTokenExpiresAt).toISOString()}`,
      );
    } else {
      logger.debug("Refresh token does not expire");
    }

    logger.debug("Saving new session to file...");
    await saveSession(newSession);
    logger.info(" Session refreshed and saved successfully");
    logger.debug("=== Token Refresh Complete ===");

    return newSession;
  } catch (error) {
    logger.error(" Failed to refresh session", error);
    if (error instanceof Error) {
      logger.debug(`Error type: ${error.constructor.name}`);
      logger.debug(`Error message: ${error.message}`);
      if (error.stack) {
        logger.debug(`Error stack: ${error.stack}`);
      }
      const timeUntilExpiry = session.expiresAt - Date.now();
      captureException(
        error,
        AUTH_TOKEN_REFRESH_FAILED,
        "session-manager",
        buildAuthProperties({
          authMethod: "token_refresh",
          timeUntilExpiry: Math.round(timeUntilExpiry / 1000),
        }),
      );
    }
    throw error;
  }
}

/**
 * Check if user is currently authenticated
 */
export async function isAuthenticated(): Promise<boolean> {
  const session = await loadSession();
  return session !== null && session.expiresAt > Date.now();
}

/**
 * Get current session (refresh if needed)
 *
 * Proactively refreshes tokens 5 minutes before expiration
 */
export async function getValidSession(): Promise<AuthSession | null> {
  const session = await loadSession();
  if (!session) {
    logger.debug("getValidSession: No session found");
    return null;
  }

  if (session.expiresAt < Date.now() + PROACTIVE_REFRESH_THRESHOLD_MS) {
    try {
      const refreshedSession = await refreshSession(session);
      return refreshedSession;
    } catch (error) {
      logger.warn("getValidSession: Failed to refresh session", error);
      return null;
    }
  }
  return session;
}
