#!/usr/bin/env node
/**
 * /zest:status command handler
 *
 * Displays authentication and sync status
 */

import { loadSession } from "../auth/session-manager.js";
import { SYNC_INTERVAL_MS } from "../config/constants.js";
import { loadSettings } from "../config/settings.js";
import { getDaemonPid } from "../utils/daemon-manager.js";
import { isFolderExcluded } from "../utils/folder-exclusion.js";
import { logger } from "../utils/logger.js";
import { getDetailedQueueStats } from "../utils/queue-manager.js";
import { readStatusCache } from "../utils/status-cache-manager.js";
import { aggregateLastHourStats } from "../utils/sync-metrics-manager.js";

/**
 * Format a timestamp as a relative time string (e.g., "30 sec ago", "2 min ago")
 */
function formatRelativeTime(timestamp: number): string {
  const now = Date.now();
  const diffMs = now - timestamp;
  const diffSec = Math.floor(diffMs / 1000);
  const diffMin = Math.floor(diffSec / 60);
  const diffHr = Math.floor(diffMin / 60);

  if (diffSec < 5) {
    return "just now";
  }
  if (diffSec < 60) {
    return `${diffSec} sec ago`;
  }
  if (diffMin < 60) {
    return `${diffMin} min ago`;
  }
  return `${diffHr} hr ago`;
}

/**
 * Format a future timestamp as relative time (e.g., "in 45 sec", "in 2 min")
 */
function formatFutureTime(timestamp: number): string {
  const now = Date.now();
  const diffMs = timestamp - now;

  if (diffMs <= 0) {
    return "now";
  }

  const diffSec = Math.floor(diffMs / 1000);
  const diffMin = Math.floor(diffSec / 60);

  if (diffSec < 60) {
    return `in ${diffSec} sec`;
  }
  return `in ${diffMin} min`;
}

async function main() {
  try {
    // Load all status data
    const session = await loadSession();
    const daemonPid = await getDaemonPid();
    const settings = await loadSettings();
    const queueStats = await getDetailedQueueStats();
    const lastHourStats = await aggregateLastHourStats();
    const statusCache = readStatusCache();

    // 1. Authentication status
    if (session) {
      console.log(` Authenticated: ${session.email}`);
    } else {
      console.log(" Not authenticated (tracking locally)");
    }

    // 2. Workspace
    if (session?.workspaceId && session?.workspaceName) {
      console.log(` Workspace: ${session.workspaceName}`);
    } else if (session) {
      console.log("  No workspace selected - run /zest:workspace");
    }

    console.log("");

    // 3. Synced in last hour (with breakdown and last sync time)
    const { synced, lastSyncAt } = lastHourStats;
    const syncedTotal = synced.sessions + synced.messages + synced.events;
    if (syncedTotal > 0) {
      const lastSyncStr = lastSyncAt ? ` (last: ${formatRelativeTime(lastSyncAt)})` : "";
      console.log(
        ` Synced (last hour): ${synced.sessions} sessions, ${synced.messages} messages, ${synced.events} code diffs${lastSyncStr}`,
      );
    } else {
      console.log(" Synced (last hour): No data synced yet");
    }

    // 4. Pending to sync (with breakdown and next sync time)
    const { sessions: sessionStats, messages: messageStats, events } = queueStats;
    const totalPending = sessionStats.total + messageStats.total + events;
    if (totalPending > 0) {
      // Calculate next sync time based on last sync (only if remote sync is enabled)
      let nextSyncStr = "";
      if (settings.enableRemotePersistence && daemonPid && lastSyncAt) {
        const nextSyncTime = lastSyncAt + SYNC_INTERVAL_MS;
        nextSyncStr = ` (next: ${formatFutureTime(nextSyncTime)})`;
      } else if (settings.enableRemotePersistence && daemonPid) {
        nextSyncStr = " (next: soon)";
      }
      console.log(
        ` Pending: ${sessionStats.total} sessions, ${messageStats.total} messages, ${events} code diffs${nextSyncStr}`,
      );

      // Show explanation if some sessions are waiting for more messages
      if (sessionStats.waitingForMessages > 0) {
        console.log(
          `      ${sessionStats.waitingForMessages} session(s) waiting for 3+ messages to sync`,
        );
      }
    } else {
      console.log(" Pending: All synced");
    }

    // 5. Sync errors
    if (lastHourStats.errors > 0 || statusCache.syncStatus.hasError) {
      console.log("");
      if (statusCache.syncStatus.hasError && statusCache.syncStatus.errorMessage) {
        console.log(` Sync error: ${statusCache.syncStatus.errorMessage}`);
      } else if (lastHourStats.errors > 0) {
        console.log(`  ${lastHourStats.errors} sync error(s) in the last hour`);
      }
    }

    console.log("");

    // 6. Daemon status
    if (daemonPid) {
      console.log(` Daemon: Running (PID: ${daemonPid})`);
    } else {
      console.log("  Daemon: Not running (will auto-start on next session)");
    }

    // 7. Remote sync status
    if (settings.enableRemotePersistence) {
      console.log(" Remote sync: Enabled");
    } else {
      console.log(" Remote sync: Disabled (local only)");
    }

    // Folder tracking status
    const currentFolder = process.cwd();
    const isExcluded = isFolderExcluded(currentFolder, settings);
    console.log("");
    console.log(" Current folder:");
    console.log(`   ${currentFolder}`);
    if (isExcluded) {
      console.log("   Status:  Ignored");
    } else {
      console.log("   Status:  Tracked");
    }
  } catch (error) {
    logger.error("Failed to get status", error);
    console.error(" Failed to get status");
    if (error instanceof Error) {
      console.error(`   ${error.message}`);
    }
    process.exit(1);
  }
}

main();
