/**
 * Sync Metrics Manager
 *
 * Tracks sync operations over time to provide "last hour" statistics.
 * Uses JSONL format for append-only writes and easy cleanup.
 * Automatically prunes entries older than 1 hour on each write.
 */

import { readFile, writeFile } from "node:fs/promises";
import { dirname } from "node:path";
import { SYNC_METRICS_FILE, SYNC_METRICS_RETENTION_MS } from "../config/constants.js";
import { withFileLock } from "./file-lock.js";
import { ensureDirectory } from "./fs-utils.js";
import { logger } from "./logger.js";
import type { SyncErrorType } from "./status-cache-manager.js";

/**
 * Single sync metric entry
 */
export interface SyncMetricEntry {
  timestamp: number; // Unix timestamp (ms)
  success: boolean;
  uploaded: {
    events: number;
    sessions: number;
    messages: number;
  };
  errorType?: SyncErrorType | null;
}

/**
 * Detailed breakdown of synced items
 */
export interface SyncedBreakdown {
  sessions: number;
  messages: number;
  events: number;
}

/**
 * Aggregated stats for the last hour
 */
export interface LastHourStats {
  synced: SyncedBreakdown; // Items successfully synced by type
  total: number; // Total items successfully synced
  errors: number; // Number of failed sync attempts
  lastSyncAt: number | null; // Timestamp of last successful sync
}

/**
 * Read all metrics from the JSONL file
 */
async function readMetrics(): Promise<SyncMetricEntry[]> {
  try {
    const content = await readFile(SYNC_METRICS_FILE, "utf8");
    const lines = content.trim().split("\n").filter(Boolean);

    const results: SyncMetricEntry[] = [];
    for (const line of lines) {
      try {
        results.push(JSON.parse(line));
      } catch {
        // Skip corrupted lines
        logger.warn("Skipping corrupted line in sync metrics file");
      }
    }
    return results;
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      return [];
    }
    throw error;
  }
}

/**
 * Write metrics to the JSONL file (overwrites)
 */
async function writeMetrics(entries: SyncMetricEntry[]): Promise<void> {
  await ensureDirectory(dirname(SYNC_METRICS_FILE));
  const content =
    entries.map((entry) => JSON.stringify(entry)).join("\n") + (entries.length > 0 ? "\n" : "");
  await writeFile(SYNC_METRICS_FILE, content, "utf8");
}

/**
 * Record a sync metric entry
 * Automatically prunes entries older than 1 hour
 */
export async function recordSyncMetric(entry: SyncMetricEntry): Promise<void> {
  try {
    await withFileLock(SYNC_METRICS_FILE, async () => {
      // Read existing metrics
      const existingMetrics = await readMetrics();

      // Prune old entries (older than 1 hour)
      const cutoffTime = Date.now() - SYNC_METRICS_RETENTION_MS;
      const recentMetrics = existingMetrics.filter((m) => m.timestamp > cutoffTime);

      // Add new entry
      recentMetrics.push(entry);

      // Write back
      await writeMetrics(recentMetrics);

      logger.debug("Recorded sync metric", {
        success: entry.success,
        uploaded: entry.uploaded,
        totalMetrics: recentMetrics.length,
      });
    });
  } catch (error) {
    // Log but don't throw - metrics are non-critical
    logger.error("Failed to record sync metric", error);
  }
}

/**
 * Get all metrics from the last hour
 */
export async function getLastHourMetrics(): Promise<SyncMetricEntry[]> {
  try {
    const allMetrics = await readMetrics();
    const cutoffTime = Date.now() - SYNC_METRICS_RETENTION_MS;
    return allMetrics.filter((m) => m.timestamp > cutoffTime);
  } catch (error) {
    logger.error("Failed to read sync metrics", error);
    return [];
  }
}

/**
 * Aggregate last hour stats for display
 * Returns detailed breakdown of items synced, error count, and last sync time
 */
export async function aggregateLastHourStats(): Promise<LastHourStats> {
  const metrics = await getLastHourMetrics();

  const synced: SyncedBreakdown = {
    sessions: 0,
    messages: 0,
    events: 0,
  };
  let errors = 0;
  let lastSyncAt: number | null = null;

  for (const metric of metrics) {
    if (metric.success) {
      synced.sessions += metric.uploaded.sessions;
      synced.messages += metric.uploaded.messages;
      synced.events += metric.uploaded.events;

      // Track the most recent successful sync
      if (lastSyncAt === null || metric.timestamp > lastSyncAt) {
        lastSyncAt = metric.timestamp;
      }
    } else {
      errors++;
    }
  }

  const total = synced.sessions + synced.messages + synced.events;

  return { synced, total, errors, lastSyncAt };
}
